import React, { useEffect, useState } from "react";
import bgImage from "./bed.png";
import "./App.css";
import { Card, Button, Typography, Layout, Popover } from "antd";
import { Col, Row } from "antd";

const { Paragraph } = Typography;
const { Content, Header } = Layout;

function App() {
  const bedData = [
    { id: 1, name: "เตียงหมายเลข 1", status: 0 },
    { id: 2, name: "เตียงหมายเลข 2", status: 0 },
    { id: 3, name: "เตียงหมายเลข 3", status: 0 },
    { id: 4, name: "ห้องผู้ป่วยติดเชื้อ", status: 0 },
    { id: 5, name: "ห้องผู้ป่วยวิกฤต", status: 0 },
    { id: 6, name: "เตียงหมายเลข 5", status: 0 },
    { id: 7, name: "เตียงหมายเลข 4.5", status: 0 },
    { id: 8, name: "เตียงหมายเลข 4", status: 0 },
    { id: 9, name: "ตรวจโรคทั่วไป", status: 0 },
    { id: 10, name: "ห้องผู้ป่วยพ่นยา", status: 0 },
    { id: 11, name: "เตียงหมายเลข 8", status: 0 },
    { id: 12, name: "เตียงหมายเลข 7", status: 0 },
    { id: 13, name: "เตียงหมายเลข 6", status: 0 },
  ];
  const statuses = [
    { id: 0, name: "ว่าง", bgColor: "white", color: "#000" },
    { id: 1, name: "ผู้ป่วยไม่ฉุกเฉิน", bgColor: "#34C724", color: "#000" },
    { id: 2, name: "ผู้ป่วยเร่งด่วน", bgColor: "yellow", color: "#000" },
    { id: 3, name: "ผู้ป่วยฉุกเฉิน", bgColor: "#ff418c", color: "#fff" },
    { id: 4, name: "ผู้ป่วยวิกฤติ", bgColor: "red", color: "#fff" },
  ];

  const openData = [
    { id: 1, value: false },
    { id: 2, value: false },
    { id: 3, value: false },
    { id: 4, value: false },
    { id: 5, value: false },
    { id: 6, value: false },
    { id: 7, value: false },
    { id: 8, value: false },
    { id: 9, value: false },
    { id: 10, value: false },
    { id: 11, value: false },
    { id: 12, value: false },
    { id: 13, value: false },
  ];

  const [beds, setBeds] = useState(bedData);
  const [open, setOpen] = useState(openData);

  const onHide = (popId) => {
    var newItems = open.map((item) => {
      if (item.id === popId) {
        item.value = false;
      }
      return item;
    });

    setOpen(newItems);
  };

  const onOpen = (popId) => {
    var newItems = open.map((item) => {
      if (item.id === popId) {
        item.value = true;
      }
      return item;
    });

    setOpen(newItems);
  };

  const onUpdateBed = (id, value) => {
    onHide(id);
    var newItems = beds.map((item) => {
      if (item.id === id) {
        item.status = value;
      }
      return item;
    });

    setBeds(newItems);
  };

  const onResetBed = () => {
    var newItems = beds.map((item) => {
      item.status = 0;
      return item;
    });
    setBeds(newItems);
  };

  const bedCard = (id) => {
    var statusId = beds.find((v) => v.id === id).status;
    var bgColor = statuses.find((s) => s.id == statusId).bgColor;
    var color = statuses.find((s) => s.id == statusId).color;

    return (
      <Card
        hoverable
        className="card"
        bordered={true}
        title={<span style={{ color: `${color}` }}>{beds.find((v) => v.id === id).name}</span>}
        style={{ backgroundColor: `${bgColor}`, padding: 1 }}
        actions={[
          <Popover
            content={
              <div style={{ width: 150 }}>
                {statuses.map((s) => (
                  <Button
                    style={{ padding: 2, margin: 2, backgroundColor: `${s.bgColor}`, color: `${s.color}` }}
                    type="default"
                    block
                    onClick={() => onUpdateBed(id, s.id)}
                  >
                    {s.name}
                  </Button>
                ))}
              </div>
            }
            //title="Title"
            trigger="click"
            open={open && open.find((v) => v.id === id).value}
            onClick={() => onOpen(id)}
          >
            <Button style={{ padding: 2 }} type="link" block onClick={(e) => ""}>
              <span style={{ fontSize: 18, fontWeight: "bold" }}>{statuses.find((s) => s.id == beds.find((v) => v.id === id).status).name}</span>
            </Button>
          </Popover>,
        ]}
      >
        <Card.Meta
          style={{ padding: 5 }}
          onClick={(e) => ""}
          description={
            <Paragraph className="card-item" ellipsis={{ rows: 2 }}>
              <img src={bgImage} style={{ width: 130, height: 70 }} />
            </Paragraph>
          }
        />
      </Card>
    );
  };

  return (
    <div className="App">
      <Layout>
        <Header className="site-layout-background" style={{ backgroundColor: "#0098ff", padding: 0, height: 50, top: 0, zIndex: 1 }}>
          <span style={{ fontSize: 30, color: "#fff" }}>โรงพยาบาลวังน้อย</span>
        </Header>
        <Layout>
          <Content style={{ margin: 0, padding: "15px 20px", textAlign: "center" }}>
            <table>
              <tr>
                <td>{bedCard(1)}</td>
                <td>{bedCard(2)}</td>
                <td>{bedCard(3)}</td>
                <td>{bedCard(4)}</td>
                <td>{bedCard(5)}</td>
              </tr>
              <tr>
                <td>{bedCard(6)}</td>
                <td>{bedCard(7)}</td>
                <td>{bedCard(8)}</td>
                <td>{bedCard(9)}</td>
                <td>&nbsp;</td>
              </tr>
              <tr>
                <td>{bedCard(10)}</td>
                <td>{bedCard(11)}</td>
                <td>{bedCard(12)}</td>
                <td>{bedCard(13)}</td>
                <td style={{verticalAlign:"bottom",padding:10}}>
                  <Button type="default" onClick={onResetBed}>
                    เคลียร์ข้อมูล
                  </Button>
                </td>
              </tr>
            </table>

            {/* <Row style={{ marginBottom: 10 }}>
              <Col flex={20}>{bedCard(1)}</Col>
              <Col flex={20}>{bedCard(2)}</Col>
              <Col flex={20}>{bedCard(3)}</Col>
              <Col flex={20}>{bedCard(4)}</Col>
              <Col flex={20}>{bedCard(5)}</Col>
            </Row>

            <Row style={{ marginBottom: 10 }}>
              <Col span={5}>{bedCard(6)}</Col>
              <Col span={5}>{bedCard(7)}</Col>
              <Col span={5}>{bedCard(8)}</Col>
              <Col span={5}>{bedCard(9)}</Col>
              <Col span={4}>
                <div style={{ width: "100%" }}>&nbsp;</div>
                
              </Col>
            </Row>
            <Row align="bottom">
              <Col span={5}>{bedCard(10)}</Col>
              <Col span={5}>{bedCard(11)}</Col>
              <Col span={5}>{bedCard(12)}</Col>
              <Col span={5}>{bedCard(13)}</Col>
              <Col span={4}>
                <Button type="default" onClick={onResetBed}>
                  เคลียร์ข้อมูล
                </Button>
              </Col>
            </Row> */}
          </Content>
        </Layout>
        {/* <Footer>footer</Footer> */}
      </Layout>
    </div>
  );
}

export default App;
